<script>
// import flatPicker from "vue-flatpickr-component";
// import "flatpickr/dist/flatpickr.css";
import DropzoneFileUpload from "@/components/Inputs/DropzoneFileUpload";
import { Select, Option } from "element-ui";
export default {
  components: {
    // flatPicker,
    DropzoneFileUpload,
    [Select.name]: Select,
    [Option.name]: Option,
  },
  data() {
    return {
      address: "",
      phone: "",
      studioName: "",
      email: "",
      isInvalid: false,
      street: "",
      city: "",
      zipCode: "",
      stateSelected: "CA",
      stateSelects: {},
      countryCodeSelected: "+1",
      countrySelects: {},
      model: {
        title: "",
        className: "bg-orange",
        description:
          "Nullam id dolor id nibh ultricies vehicula ut id elit. Cum sociis natoque penatibus et magnis dis parturient montes, nascetur ridiculus mus.",
        start: "",
        end: "",
      },
      eventColors: [
        "bg-info",
        "bg-orange",
        "bg-red",
        "bg-green",
        "bg-default",
        "bg-blue",
        "bg-purple",
        "bg-yellow",
      ],
      inputs: {
        tags: ["BUCHAREST", "IASI", "TIMISOARA"],
        fileSingle: [],
        fileMultiple: [],
      },
    };
  },
  watch: {
    countryCodeSelected(val, old) {
      let self = this;
      document.getElementsByClassName(
        "tk-select-border-none"
      )[0].childNodes[1].childNodes[1].style.display = "none";
      self.countrySelects.options.some((item) => {
        if (item.value === val) {
          setTimeout(() => {
            document.getElementsByClassName(
              "tk-select-border-none"
            )[0].childNodes[1].childNodes[1].value = item.areaCode;
            document.getElementsByClassName(
              "tk-select-border-none"
            )[0].childNodes[1].childNodes[1].style.display = "block";
          }, 10);
        }
      });
    },
  },
  created() {
    let self = this
    self.countrySelects = self.$tools.countrySelects
    self.stateSelects = self.$tools.stateSelects
  },
  mounted() {
    let self = this;
    setTimeout(function () {
      document.getElementById("uploadLogo").childNodes[1].innerText =
        "Drop logo here to upload";
      document.getElementById("uploadLogo").childNodes[1].style.height =
        "210px";
      document.getElementById("uploadLogo").childNodes[1].style.lineHeight =
        "56px";
      document.getElementById("storefrontColor").style.marginTop = "16px";
    }, 200);
    // console.log(document.getElementsByClassName("tk-select-border-none")[0].childNodes[1].childNodes);
  },
  methods: {
    validationEmail() {
      let self = this;
      if (self.$tools.isNull(self.email)) {
        self.isInvalid = false;
      } else {
        self.isInvalid = !self.$tools.isEmail(self.email);
      }
    },
  },
};
</script>